  const [day, setday] = useState("");
  const [month, setmonth] = useState("");
  const [year, setyear] = useState("");

  function getCurDate() {
    const today = new Date();
    let tomorrow = new Date(today);
    tomorrow.setDate(tomorrow.getDate() + 2);

    let monthIndex = tomorrow.getMonth();
    let monthArray = [
      "january",
      "february",
      "march",
      "april",
      "may",
      "june",
      "july",
      "august",
      "september",
      "october",
      "november",
      "december",
    ];
    let monthName = monthArray[monthIndex];
    let formattedMonth = monthName.slice(0, 3); // get the first three letters of the month

    let formattedDay = tomorrow.getDate().toString().padStart(2, "0"); // pad the day with leading zero if needed

    setday(formattedDay);
    setmonth(formattedMonth);
    setyear(tomorrow.getFullYear());
  }

  useEffect(() => {
    getCurDate();
  }, []);

