
    
    import React, { useState, useEffect } from "react";

    const Tula = (props) => {
      const [currentItem, setCurrentItem] = useState(0);
      const [isSliderVisible, setIsSliderVisible] = useState(false);
      const [textareaValue2, setTextareaValue] =  useState(localStorage.getItem("textareaValue2")|| "") ;
      const [luckyText, setLuckyText] = useState("");
    
      // Function to generate a random number between 1 and 100
      const getRandomNumber = () => {
        return Math.floor(Math.random() * 100) + 1;
      };
    
      // Array of Telugu colour names
      const colours = [
        "తెలుపు",
      "బ్లాక్",
      "నలుపు",
      "బ్లూ",
      "పసుపు",
      "ఎరుపు",
      "పచ్చ",
      "గులాబీ",
      "గోధుమ",
      "ఊదా",
      "బూడిద",
      "నారింజ",
      "బంగారు",
      "మెరూన్",
      "రూబీ",
      "నేవీ బ్లూ",
      "ఆకాశనీలం",
      "మట్టి",
      "వెండి",
      "లేత గోధుమరంగు",
      "కాంస్య",
      "తెలుపు",
      "లోహ",
      "మణి",
      "కాషాయం",
      "తుప్పు",
      "ద్రాక్ష",
      "రేగు",
      "పుదీనా",
      "సున్నం",
      "ఆలివ్",
      "దంతము",
      "వైలెట్",
      "సియాన్",
      "బఠానీ ఆకుపచ్చ",
      "మెజెంటా",
      "పగడపు",
      "టీల్",
      "ఆవాలు",
      "గోధుమ",
      ];
    
      // Function to generate a random colour name in Telugu
      const getRandomColour = () => {
        const randomIndex = Math.floor(Math.random() * colours.length);
        return colours[randomIndex];
      };
    
      // Update the luckyText state variable when the component mounts
      useState(() => {
        const randomNumber = getRandomNumber();
        const randomColour = getRandomColour();
        setLuckyText(`లక్కి సంఖ్య: ${randomNumber} లక్కి కలర్: ${randomColour}`);
      }, []);
      
      
      const items = [
        {
          id: 1,
          title: "11.Rasi phalalu today telugu tula",
          thumbnail: props.thumb2,
          src: "https://www.astrosage.com/telugu/rasi-phalalu/kanya-tomorrow-rasi-phalalu.asp",
        },
        {
          id: 2,
          title: "తుల రాసి Extra 1",
          thumbnail: "",
          src: "https://www.andhrajyothy.com/astrology",
        },
        {
          id: 3,
          title: "తుల రాసి Extra 2",
          thumbnail: "",
          src: "https://www.dishadaily.com/horoscope",
        },
        {
          id:4,
          title: "తుల రాసి Extra 3",
          thumbnail: "",
          src: "https://telugu.abplive.com/topic/rasi-phalalu",
        },
        
        {
          id: 5,
          title: "తుల రాసి Extra 4",
          thumbnail: "",
          src: "https://telugu.hindustantimes.com/topic/rasi-phalalu-today",
        },
      ];
    
      // Load text area value from local storage on mount
      let updater = ()=>
      {
        let today =  new Date().getDate();
        let yesterday = localStorage.getItem('date9') || "";
        if(today != yesterday){
          setTextareaValue(items[0].thumbnail+ ". "+textareaValue2.replace(":"," వారికి ")+ " "+luckyText);
          localStorage.setItem("textareaValue2",items[0].thumbnail+ ". "+textareaValue2.replace(":"," వారికి ")+ " "+luckyText); 
        }
        else{
          setTextareaValue(localStorage.getItem("textareaValue2") || "");

        }

        
      }
    
        const DailyOnceSet = function(){
          let currentDate = new Date();
          let currentDay = currentDate.getDate();
          localStorage.setItem('date9', currentDay);
          }
        // Load text area value from local storage on mount
        useEffect(() => {

          // localStorage.setItem('date9', 20);
         updater();
         DailyOnceSet();
      
        }, []);
      // Function to handle text area value and save it to local storage
      const handleTextarea = (e) => {
        setTextareaValue(e.target.value);
        localStorage.setItem("textareaValue2", e.target.value);
    
      };
    
      const handleNext = () => {
        if (currentItem === items.length - 1) {
          setCurrentItem(0);
        } else {
          setCurrentItem(currentItem + 1);
        }
      };
    
      const handlePrev = () => {
        if (currentItem === 0) {
          setCurrentItem(items.length - 1);
        } else {
          setCurrentItem(currentItem - 1);
        }
      };
      function handlePaste(){
        navigator.clipboard.readText().then((text)=>{
          setTextareaValue(textareaValue2+text+ " ");
        localStorage.setItem("textareaValue2", textareaValue2+text+ " ");

        })
      }
      const handleShowSlider = () => {
        setIsSliderVisible(true);
      };
    
      const handleSave = () => {
        setTextareaValue(items[0].thumbnail+ ". "+textareaValue2+ " "+luckyText);
        localStorage.setItem("textareaValue2",items[0].thumbnail+ ". "+textareaValue2+ " "+luckyText); 
      };

      const handleSave2 = () => {
        setTextareaValue(textareaValue2.replace("కన్య","తుల").replace("కన్యా","తులా").replace(":"," వారికి ").replace("అనుకూలంగా లేదు","అనుకూలముగా ఉన్నది"));
        localStorage.setItem("textareaValue2", textareaValue2.replace("కన్య","తుల").replace("కన్యా","తులా").replace(":"," వారికి ").replace("అనుకూలంగా లేదు","అనుకూలముగా ఉన్నది")); 
      };
      
      const handleClear = () => {
        localStorage.removeItem("textareaValue2");
        setTextareaValue("");
      };
    
      return (
        <div className="container">
          <div>
            <h4>11.Tula rasi phalalu today telugu</h4>
          </div>
          <textarea id = "t2" rows = "30" cols = "auto" value={textareaValue2} onChange={handleTextarea} />
          <button className="mx-2 my-2" onClick={handleSave}>Update</button>
          <button className="mx-2 my-2" onClick={handleSave2}>Save</button>
          <button className="mx-2 my-2" onClick={handleClear}>Clear</button>
          <button className="mx-2 my-3" onClick={() =>handlePaste()}>
            Paste
          </button>
          <button className="mx-2 my-2"onClick={handleShowSlider}>On</button>
          <div className={`slider ${isSliderVisible ? "" : "hidden"}`}>
            <div className="card">
              <h5 className="card-title danger">{items[currentItem].title}</h5>
              <div className="card-body">
                <h3>{items[currentItem].thumbnail}</h3>
                <iframe
                  className="card-img-top"
                  style={{ width: "100%", height: "60rem" }}
                  width="80"
                  height="160"
                  src={items[currentItem].src}
                ></iframe>
              </div>
            </div>
            <div className="slider-nav">
              <button className="mx-3 my-3"onClick={handlePrev}>Prev</button>
              <button className="mx-3 my-3"onClick={handleNext}>Next</button>
            </div>
          </div>
        </div>
      );
    };
    
    export default Tula
    