


import React, { useState, useEffect } from "react";

const Mesha = (props) => {
  const [currentItem, setCurrentItem] = useState(0);
  const [isSliderVisible, setIsSliderVisible] = useState(false);
  const [luckyText, setLuckyText] = useState("");

  // Function to generate a random number between 1 and 100
  const getRandomNumber = () => {
    return Math.floor(Math.random() * 100) + 1;
  };

  // Array of Telugu colour names
  const colours = [
    "తెలుపు",
  "బ్లాక్",
  "నలుపు",
  "బ్లూ",
  "పసుపు",
  "ఎరుపు",
  "పచ్చ",
  "గులాబీ",
  "గోధుమ",
  "ఊదా",
  "బూడిద",
  "నారింజ",
  "బంగారు",
  "మెరూన్",
  "రూబీ",
  "నేవీ బ్లూ",
  "ఆకాశనీలం",
  "మట్టి",
  "వెండి",
  "లేత గోధుమరంగు",
  "కాంస్య",
  "తెలుపు",
  "లోహ",
  "మణి",
  "కాషాయం",
  "తుప్పు",
  "ద్రాక్ష",
  "రేగు",
  "పుదీనా",
  "సున్నం",
  "ఆలివ్",
  "దంతము",
  "వైలెట్",
  "సియాన్",
  "బఠానీ ఆకుపచ్చ",
  "మెజెంటా",
  "పగడపు",
  "టీల్",
  "ఆవాలు",
  "గోధుమ",
  ];

  // Function to generate a random colour name in Telugu
  const getRandomColour = () => {
    const randomIndex = Math.floor(Math.random() * colours.length);
    return colours[randomIndex];
  };

  // Update the luckyText state variable when the component mounts
  useState(() => {
    const randomNumber = getRandomNumber();
    const randomColour = getRandomColour();
    setLuckyText(`లక్కి సంఖ్య: ${randomNumber} లక్కి కలర్: ${randomColour}`);
  }, []);

  const items = [
    {
      id: 1,
      title: "6.Rasi Phalalu today Mesha",
      thumbnail: props.thumb3,
      src: "https://www.astrosage.com/telugu/rasi-phalalu/mesha-tomorrow-rasi-phalalu.asp",
    },
    {
      id: 2,
      title: "మేష రాసి Extra 1",
      thumbnail: "",
      src: "https://www.andhrajyothy.com/astrology",
    },
    {
      id: 3,
      title: "మేష రాసి Extra 2",
      thumbnail: "",
      src: "https://www.dishadaily.com/horoscope",
    },
    {
      id:4,
      title: "మేష రాసి Extra 3",
      thumbnail: "",
      src: "https://telugu.abplive.com/topic/rasi-phalalu",
    },
    
    {
      id: 5,
      title: "మేష రాసి Extra 4",
      thumbnail: "",
      src: "https://telugu.hindustantimes.com/topic/rasi-phalalu-today",
    },
  ];

  // Load text area value from local storage on mount
  let updater = ()=>
  {
    let today =  new Date().getDate();
    let yesterday = localStorage.getItem('date6') || "";
    if(today != yesterday){
      props.setTextareaValue1(items[0].thumbnail+ ". "+(props.textareaValue7).replace(":"," వారికి ")+ " "+luckyText);
      localStorage.setItem("props.textareaValue7",items[0].thumbnail+ ". "+props.textareaValue7.replace(":"," వారికి ")+ " "+luckyText); 
    }
    else{
    props.setTextareaValue1(localStorage.getItem("props.textareaValue7") || "");
    }
  }

    const DailyOnceSet = function(){
      let currentDate = new Date();
      let currentDay = currentDate.getDate();
      localStorage.setItem('date6', currentDay);
      }
    // Load text area value from local storage on mount
    useEffect(() => {

      // localStorage.setItem('date6', 20);
     updater();
     DailyOnceSet();
  
    }, []);


  // Function to handle text area value and save it to local storage
  const handleTextarea = (e) => {
    props.setTextareaValue1(e.target.value);
    localStorage.setItem("props.textareaValue7", e.target.value);

  };

  const handleNext = () => {
    if (currentItem === items.length - 1) {
      setCurrentItem(0);
    } else {
      setCurrentItem(currentItem + 1);
    }
  };

  const handlePrev = () => {
    if (currentItem === 0) {
      setCurrentItem(items.length - 1);
    } else {
      setCurrentItem(currentItem - 1);
    }
  };
  function handlePaste(){
    navigator.clipboard.readText().then((text)=>{
      props.setTextareaValue1(props.textareaValue7+text+ " ");
    localStorage.setItem("props.textareaValue7", props.textareaValue7+text+ " ");

    })
  }
  const handleShowSlider = () => {
    setIsSliderVisible(true);
  };

  const handleSave = () => {
    props.setTextareaValue1(items[0].thumbnail+ ". "+(props.textareaValue7)+ " "+luckyText);
    localStorage.setItem("props.textareaValue7",items[0].thumbnail+ ". "+props.textareaValue7+ " "+luckyText); 
  };
  const handleSave2 = () => {
    props.setTextareaValue1(props.textareaValue7.replace("అనుకూలంగా లేదు","అనుకూలముగా ఉన్నది").replace(":"," వారికి "));
    localStorage.setItem("props.textareaValue7", props.textareaValue7.replace("అనుకూలంగా లేదు","అనుకూలముగా ఉన్నది").replace(":"," వారికి ")); 
  };
  
  const handleClear = () => {
    localStorage.removeItem("props.textareaValue7");
    props.setTextareaValue1("");
  };

  return (
    <div className="container">
      <div>
        <h4>6.Mesha</h4>
      </div>
      <textarea id = "t7" rows = "30" cols = "auto" value={props.textareaValue7} onChange={handleTextarea} />
      <button className="mx-2 my-2" onClick={handleSave}>Update</button>
      <button className="mx-2 my-2" onClick={handleSave2}>Save</button>
      <button className="mx-2 my-2" onClick={handleClear}>Clear</button>
      <button className="mx-2 my-3" onClick={() =>handlePaste()}>
            Paste
          </button>
      <button className="mx-2 my-2"onClick={handleShowSlider}>On</button>
      <div className={`slider ${isSliderVisible ? "" : "hidden"}`}>
        <div className="card">
          <h5 className="card-title danger">{items[currentItem].title}</h5>
          <div className="card-body">
            <h3>{items[currentItem].thumbnail}</h3>
            <iframe
              className="card-img-top"
              style={{ width: "100%", height: "60rem" }}
              width="80"
              height="160"
              src={items[currentItem].src}
            ></iframe>
          </div>
        </div>
        <div className="slider-nav">
          <button className="mx-3 my-3"onClick={handlePrev}>Prev</button>
          <button className="mx-3 my-3"onClick={handleNext}>Next</button>
        </div>
      </div>
    </div>
  );
};

export default Mesha
